<template>
    <div class="">
        <div class="row">
            <div class="col-xs-12">
                <button @click="limpiar" class="btn btn-primary pull-right" data-backdrop="static" data-toggle="modal" data-target="#modal">
                    Nuevo Cliente <i class="fa fa-user-plus"></i>
                </button>
            </div>
        </div>
        <c-data-tables @paginate="send" @refresh="refresh" title="Listado de clientes" :limite="[10,20,50,100]" :total="total" :opt="opts">
            <template v-slot:table>
                <table class="table table-bordered table-condensed table-hover table-striped">
            <thead class="text-bold">
                <tr>
                    <th class="col-xs-1">Codigo</th>
                    <th style="text-overflow-ellipsis: 1">Nombre</th>
                    <th>Telefono</th>
                    <th style="text-overflow-ellipsis: 1">Direccion</th>
                    <th>Ocupacion</th>
                    <th class="col-xs-1">Edad</th>
                    <th class="col-xs-1">Sexo</th>
                    <th>Fecha. Nac</th>
                    <th style="width: 60px"></th>
                </tr>
                <tr>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model.number="values.id" type="number" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.id=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.nombre" type="text" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.nombre=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.telefono" type="tel" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.telefono=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.direccion" type="text" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.direccion=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model="values.ocupacion" type="text" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.ocupacion=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model.number="values.edad" type="number" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.edad=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th>
                        <select @change="send" v-model="values.sexo" class="form-control input-sm">
                            <option value="">Todos</option>
                            <option value="h" >Hombre</option>
                            <option value="m">Mujer</option>
                        </select>
                    </th>
                    <th>
                        <div class="input-group">
                            <input @keyup.enter="send" v-model.number="values.fecha_nac" type="date" class="form-control input-sm">
                            <span class="input-group-btn">
                                <button type="button" @click="values.fecha_nac=null" class="btn btn-default btn-sm">
                                    <span class="fa fa-remove"></span>
                                </button>
                            </span>
                        </div>
                    </th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(m,i) in model" :key="m.id">
                    <td>{{m.id}}</td>
                    <td>{{m.nombre}}</td>
                    <td>{{m.telefono}}</td>
                    <td>{{m.direccion}}</td>
                    <td>{{m.ocupacion}}</td>
                    <td>{{m.edad}}</td>
                    <td>{{m.sexo=='h'?'Hombre':'Mujer'}}</td>
                    <td>{{m.fecha_nac}}</td>
                    <td>
                        <button v-if="$can('admin')" @click="confirmarDelete(m.id,i)" class="btn btn-danger btn-xs"><span class="fa fa-trash"></span></button>
                        <button @click="editar(m,i)" class="btn btn-primary btn-xs"><span class="fa fa-pencil"></span></button>
                    </td>
                </tr>
            </tbody>
        </table>
            </template>
        </c-data-tables>
        <!--agregar nuevo cliente-->
        <t-clientes :model="client" @store="storeEvent" @update="updateEvent"/>
        <modal-delete :model="confirmar" @onDelete="eliminar"/>

    </div>
</template>

<script>
    import tClientes from '@/components/templates/t-clientes'
    import modalDelete from '@/components/el/c-delete'
    import api from '@/services'
    export default {
        name: "clientes",
        components:{tClientes,modalDelete},
        data(){
          return {
              loading:false,
              model:[],
              confirmar:{id:0,index:null},
              edit:null,
              client: {
                  id:0,
                  nombre:null,
                  telefono:null,
                  direccion:null,
                  ocupacion:null,
                  edad:null,
                  sexo:null,
                  fecha_nac:null
              },
              values: {
                  id:null,
                  nombre:null,
                  telefono:null,
                  direccion:null,
                  ocupacion:null,
                  edad:null,
                  sexo:'',
                  fecha_nac:null
              },
              total:0,
              opts:{
                  per_page:10,
                  page:1,
              }
          }
        },
        mounted(){
            this.$emit('title','Clientes');
        },
        methods: {
            onSubmit(){
                this.loading = true;
            },
            storeEvent(e){
                this.model.unshift(e);
                this.total +=1;
            },
            updateEvent(m){
                this.model.splice(this.edit,1,m);
                this.edit = null;
                this.limpiar();
            },
            confirmarDelete(id,i){
                this.confirmar.id = id;
                this.confirmar.index = i;
                window.$('#modal-delete').modal({
                    backdrop:false,
                    show:true
                });
            },
            editar(m,i){
                this.setModel(m);
                this.edit = i;
                window.$('#modal').modal('show');
            },
            eliminar(obj){
                api.clientes.destroy(obj.id).then((r)=>{
                    if (r.data){
                        this.$toast.warning();
                        this.model.splice(obj.index,1);
                    } else {
                        this.$toast.error('No se puedo eliminar el registro');
                    }
                }).catch(()=>{
                    this.$toast.catch();
                });
            },
            setModel(m){
                this.client.id = m.id;
                this.client.nombre = m.nombre;
                this.client.telefono = m.telefono;
                this.client.direccion = m.direccion;
                this.client.ocupacion = m.ocupacion;
                this.client.edad = m.edad;
                this.client.sexo = m.sexo;
                this.client.fecha_nac = m.fecha_nac;
            },
            limpiar(){
                this.client.id = 0;
                this.client.nombre = null;
                this.client.telefono = null;
                this.client.direccion = null;
                this.client.ocupacion = null;
                this.client.edad = null;
                this.client.sexo =null;
                this.client.fecha_nac = null;
            },
            //datatables
            send(){
                api.clientes.search(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            refresh(){
               for (let k in this.values){
                    this.values[k]==null;
               }
               this.values.sexo = '';
               this.send();
            },
            getParams(){
                return {...this.opts,params:this.values};
            }
        }
    }
</script>